.transaction-table {
  width: 100%;
  border-collapse: collapse;
}

.transaction-table th, .transaction-table td {
  border: 1px solid #ddd;
  padding: 8px;
}

.transaction-table th {
  cursor: pointer;
  background-color: #f2f2f2;
}

.transaction-table th:hover {
  background-color: #e2e2e2;
}
